@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #29486e;    
    --color-bg-variant: #193960;
    --color-primary: #0f9ff3;
    --color-primary-variant: rgb(93 173 255 / 40%);
    --color-white: #7ac0fb;
    --color-light: rgba(255, 255, 255, 0.726);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: "Poppins", sans-serif;
    color: var(--color-white);
    line-height: 1.7;
    background: linear-gradient(0deg, rgba(2,0,36,1) 0%, rgb(27, 49, 82) 85%, rgb(11, 38, 54) 100%);
}

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5;
}

section {
    margin-top: 8rem;
    
}

section>h2,
section>h5 {
    text-align: center;
    color: var(--color-light);
    
}

section>h2 {
    color: var(--color-primary);
    font-size: 3rem;
    margin-bottom: 3.5rem;
    text-shadow: 2px 2px #0b325a;
    
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}


.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
    
}


@media screen and (max-width: 600px) {
    .container{
        width: var(--container-width-sm);
    }
    section{
        margin-bottom: 2rem;
    }
    
}