.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}
.btn_about{
    margin-top: 50px;
    margin-left: 40%;
    
}

.imgs{
    height: 500px;
}
.about__me {
    width: 100%;
    
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,
            transparent,
            var(--color-primary),
            transparent);
    display: grid;
    place-items: center;
}



.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: #0f4b75;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
.about__icon{
    width: 80px;
    height: 100px;
}

/* ================ MEDIA QUERIES (MEDIUM DEVICES)
========= */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
        
    }
   

    .about__me {
        width: 400px;
        margin: 2rem auto 2rem;
    }

    .about__content p {
        margin: 1rem 01.5rem;
    }
   
}



/* ================ MEDIA QUERIES (SMALL DEVICES)
==== ========= */
@media screen and (max-width: 600px) {
    .about__me {
        width: 200px;
        margin: 2rem auto 2rem;
        
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }
    .about__content p {
        margin: 1.5rem 0;
    }
    about__me-image{
        width: 50%;
    }
}